import { Card, CardContent, Stack, Typography } from '@mui/material'
import React from 'react'
import ProjectCard from './ProjectCard'

const Plugins = () => {
  return (
    <Stack direction="row" justifyContent="center">
      <Stack spacing={3} mt={7} mb={5}>
        <Stack direction="row" alignItems="baseline" spacing={2} mb={3}>
          <Typography variant="h3" sx={{fontWeight:300}}>
            plug-ins
          </Typography>
          <Typography variant="h4" color = "textSecondary" sx={{fontWeight:300}}>
            VST3/AU
          </Typography>
        </Stack>
        <ProjectCard
          name="DR-1"
          img="https://raw.githubusercontent.com/tparker48/tparker48.github.io/master/images/DR-1.png"
          info=" Drone Synthesizer"
          destination=""
        />
        <ProjectCard
          name="LV-1"
          img="https://raw.githubusercontent.com/tparker48/tparker48.github.io/master/images/LV-1.png"
          info=" Winds/Flute Synthesizer "
          destination=""
        />
        <ProjectCard
          name="Sherpa"
          img="https://raw.githubusercontent.com/tparker48/tparker48.github.io/master/images/sherpa.png"
          info=" Topograhpic Synthesizer "
          destination=""
        />
      </Stack>
    </Stack>
  )
}

export default Plugins